.PersonalData{
    display: flex;
    flex-direction: column;
    height: 330px;
    border-radius: 10px;
}
.top{
    display: flex;
    background-color:white;
    height: 50%;
    justify-content: center;
    border-bottom: 1px solid darkgray;
}
.center{
    background-color: white;
    display: flex;
    align-items: flex-end;
    height: 12%;
}
.center .container div{
    display: flex;
}
.bottom{
    background-color: white;
    height: 38%;
}
.avatar{
    display: flex;
    flex-direction: column;
}
.image{
    height: 100px;
    width: 100px;
    margin-top: 20px;
    margin-bottom: 10px;
    border: 2px dotted #53787D;
}
.name{
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-size: 17px;
    color:#53787D;
}
.icon{
    width: 22px;
    height: 22px;
}
.icons{
    flex: 1;
    display: flex;
    justify-content: center;
}
.icon:hover{
    cursor: pointer;
}
.bottom{

}
.synopsis{
    color:darkgray;
    font-size: 13px;
    display: flex;
    justify-content: center;
    margin-top:10px;
}
.bottom .container{
    display: flex;
    flex-direction: column;
    margin: 15px;
}
.statistics{
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    width: 80%;
}
.release{
   width: 33%;
}
.comment{
    width: 33%;
}
.browse{
    width: 33%;
}
.indivTop{
    font-size: 10px;
    display: flex;
    justify-content:center;
}
.indivBottom{
    display: flex;
    justify-content: center;
}
