body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {

}
.head{

}



.head nav{
    background-color: white;
    background-image: none;
}
.logo p{
    color: #70B991;
    font-size:23px;
    font-weight:bold;
}
.a p{
    color:white;
}
.a{
    margin:0;
    line-height:1.0;
}
.a:hover{
    background-color: #70B991;
}

.PersonalData{
    display: flex;
    flex-direction: column;
    height: 330px;
    border-radius: 10px;
}
.top{
    display: flex;
    background-color:white;
    height: 50%;
    justify-content: center;
    border-bottom: 1px solid darkgray;
}
.center{
    background-color: white;
    display: flex;
    align-items: flex-end;
    height: 12%;
}
.center .container div{
    display: flex;
}
.bottom{
    background-color: white;
    height: 38%;
}
.avatar{
    display: flex;
    flex-direction: column;
}
.image{
    height: 100px;
    width: 100px;
    margin-top: 20px;
    margin-bottom: 10px;
    border: 2px dotted #53787D;
}
.name{
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-size: 17px;
    color:#53787D;
}
.icon{
    width: 22px;
    height: 22px;
}
.icons{
    flex: 1 1;
    display: flex;
    justify-content: center;
}
.icon:hover{
    cursor: pointer;
}
.bottom{

}
.synopsis{
    color:darkgray;
    font-size: 13px;
    display: flex;
    justify-content: center;
    margin-top:10px;
}
.bottom .container{
    display: flex;
    flex-direction: column;
    margin: 15px;
}
.statistics{
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    width: 80%;
}
.release{
   width: 33%;
}
.comment{
    width: 33%;
}
.browse{
    width: 33%;
}
.indivTop{
    font-size: 10px;
    display: flex;
    justify-content:center;
}
.indivBottom{
    display: flex;
    justify-content: center;
}

.articleClassification{
    background-color: white;
    margin-top: 30px;
    border-radius: 4px;
}
.optionLink{
    color: #53787D;
}
.optionLink:hover{
    text-decoration: none;
    cursor:pointer;
    color: #70B991;
}
.displayPanel{
    background-color: white;
    height: 1000px;
}
.footer{
    margin-top: 30px;
    height: 70px;
    display: flex;
    background-color: white;
    justify-content: center;
}
.content{
    margin-right: auto;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.time{
    text-align: center;
    color: #53787D;
}
